import { css } from "styled-components";
import { cvTheme } from "../../../themes/CVThemeProvider";
export var TextWeightsEnum;
(function (TextWeightsEnum) {
    TextWeightsEnum["BASE"] = "base";
    TextWeightsEnum["BOLD"] = "bold";
    TextWeightsEnum["BOOK"] = "book";
})(TextWeightsEnum || (TextWeightsEnum = {}));
export var TextSizesEnum;
(function (TextSizesEnum) {
    TextSizesEnum["SMALL"] = "sm";
    TextSizesEnum["MEDIUM"] = "md";
    TextSizesEnum["LARGE"] = "lg";
})(TextSizesEnum || (TextSizesEnum = {}));
export const textDefaultProps = {
    color: cvTheme.text.default,
    theme: {
        fontWeights: cvTheme.fontWeights,
        bodyFontSizes: cvTheme.bodyFontSizes,
        lineHeight: cvTheme.lineHeight,
        text: cvTheme.text,
    },
    weight: TextWeightsEnum.BASE,
};
export const textBase = css `
    color: ${(props) => props.color || props.theme.text.default};
    line-height: ${(props) => props.theme.lineHeight};
    font-weight: ${(props) => {
    if (props.weight === TextWeightsEnum.BOLD) {
        return props.theme.fontWeights.bold;
    }
    if (props.weight === TextWeightsEnum.BOOK) {
        return props.theme.fontWeights.book;
    }
    return props.theme.fontWeights.normal;
}};
`;



